.toggle-menu {
  display         : block;
  margin          : 0;
  padding         : 0;
  border          : none;
  background-color: transparent;
  justify-self    : end;
}

.toggle-menu--menu {
  transition: transform .6s cubic-bezier(.075, .82, .165, 1), opacity .6s cubic-bezier(.075, .82, .165, 1);
}

.nav-open .toggle-menu--menu {
  transform: scale(0);
  opacity  : 0;
}

.toggle-menu--cross {
  margin-top  : 21px;
  margin-right: -12px;
  transform   : scale(0);
  transition  : transform .6s cubic-bezier(.075, .82, .165, 1), opacity .6s cubic-bezier(.075, .82, .165, 1), stoke .6s cubic-bezier(.075, .82, .165, 1);
  opacity     : 0;
  z-index     : 1;
}

.menu--open .toggle-menu--cross {
  transform: scale(1);
  opacity  : 1;
}


@media (max-width: 767px) {
  .toggle-menu--cross {
    margin-top: 10px;
  }
}
