.hero__wrapper {
  display       : flex;
  flex-direction: column;
}

.hero {
  display            : grid;
  grid-template-rows : 1fr min-content;
  min-height         : 100%;
  background-color   : var(--grey-hero);
  background-image   : url("../img/bg-hero.jpg");
  background-repeat  : no-repeat;
  background-size    : cover;
  background-position: center bottom;
}

.hero__promo {
  max-width       : 799px;
  padding-top     : 80px;
  padding-left    : 40px;
  padding-right   : 105px;
  padding-bottom  : 128px;
  background-color: var(--green);
}

.hero__title {
  margin        : 0;
  margin-bottom : 32px;
  font-weight   : 900;
  font-size     : 60px;
  line-height   : 72px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}

.hero__text {
  margin: 0;
}

@media (max-width: 1560px) {

  .hero__promo {
    width: 54%;
  }
}

@media (max-width: 1280px) {

  .hero__promo {
    padding-top   : 56px;
    padding-bottom: 56px;
  }
}

@media (max-width: 1279px) {

  .hero__promo {
    padding-left : 24px;
    padding-right: 24px;
  }

  .hero__title {
    margin-bottom: 24px;
    font-size    : 42px;
    line-height  : 130%;
  }

  .hero__text {
    font-size: 14px;
  }
}

@media (max-width: 768px) {
  .hero {
    background-image: url("../img/bg-hero-tablet.jpg");
  }
}

@media (max-width: 767px) {
  .hero__promo {
    width        : 100%;
    order        : -1;
    margin-bottom: 15px;
  }

  .hero__wrapper {
    padding-bottom: 87px;
  }

  .hero__title {
    margin-bottom: 8px;
    font-size    : 30px;
  }
}

@media (max-width: 375px) {
  .hero {
    background-image: url("../img/bg-hero-mobile.jpg");
  }
}
