.splide {
  margin-left: auto;
}

.splide__slide {
  position       : relative;
  display        : flex;
  justify-content: space-between;
  padding-left   : 56px;
  background     : var(--purple);

  &::after {
    content     : "";
    position    : absolute;
    width       : 0;
    height      : 0;
    border-right: 90px solid transparent;
    border-top  : 256px solid var(--purple);
    right       : 334px;
  }
}

.splide__descr {
  max-width  : 450px;
  padding-top: 104px;
}

.splide__title {
  margin       : 0;
  margin-bottom: 16px;
  font-weight  : 700;
  font-size    : 24px;
  line-height  : 32px;
  color        : var(--green);
}

.splide__text {
  margin : 0;
  color  : var(--green);
  opacity: 0.75;
}

.splide__pagination {
  top          : 56px;
  bottom       : auto;
  left         : 56px;
  width        : auto;
  transform    : translateX(0);
  counter-reset: pagination;
}

.splide__pagination__page {
  width            : 18px;
  height           : 24px;
  counter-increment: pagination;
  background       : transparent;
  opacity          : 0.25;

  &::before {
    content    : counter(pagination, decimal-leading-zero);
    font-family: 'Source Sans Pro', sans-serif;
    font-size  : 16px;
    line-height: 24px;
    color      : var(--white);
    opacity    : 0.25;
  }
}

.splide__pagination__page.is-active {
  background: transparent;
  opacity   : 1;
  transform : scale(1);

  &::before {
    font-weight: 700;
    color      : var(--green);
    opacity    : 1;
  }
}

.splide__arrow svg {
  width : 16px;
  height: 16px;
  fill  : none;
}

.splide__arrow {
  width        : 44px;
  height       : 64px;
  border       : 1px solid var(--white);
  border-radius: 0;
  background   : transparent;
  opacity      : 1;
  transition   : 0.35s;

  &:hover {
    border-color    : var(--green);
    background-color: var(--green);
  }

  &--prev,
  &--next {
    transform: skew(-20deg) translateY(-50%);
  }

  &--prev {
    left: -86%;
  }

  &--next {
    right: auto;
    left : -79%;
  }
}

@media (max-width: 1560px) {
  .splide__slide {
    padding-bottom: 56px;
  }

  .splide__slide img,
  .splide__slide::after {
    display: none;
  }
}

@media (max-width: 1440px) {
  .splide__arrow {
    &--prev {
      left: -88%;
    }
  }
}

@media (max-width: 1280px) {

  .splide__slide {
    padding-bottom: 24px;
  }

  .splide__descr {
    padding-top: 60px;
  }

  .splide__pagination {
    top: 24px;
  }

  .splide__arrow {
    &--prev {
      left: -99%;
    }

    &--next {
      left: -88%;
    }
  }

  .splide__pagination {
    left: 51px;
  }
}

@media (max-width: 1279px) {
  .splide__slide {
    padding-right: 32px;
    padding-left : 32px;
  }

  .splide__title {
    font-size    : 18px;
    margin-bottom: 2px;
  }

  .splide__text {
    font-size: 14px;
  }

  .splide__pagination {
    left: 30px;
  }

  .splide__container {
    max-width: 100vw;
  }
}

@media (max-width: 768px) {
  .splide__arrow--prev {
    left: -107%;
  }
}

@media (max-width: 767px) {
  .splide__container {
    order     : -1;
    align-self: end;
    max-width : 100vw;
    padding   : 0 24px;
    margin    : 0 auto;
  }

  .splide {
    margin-left: 0;
  }

  .splide__title {
    font-size    : 14px;
    line-height  : 24px;
    margin-bottom: 6px;
  }

  .splide__pagination__page::before {
    font-size: 14px;
  }
}
