.page-body {
  min-width  : 320px;
  margin     : 0;
  padding    : 0;
  font-size  : 16px;
  line-height: 24px;
  font-family: 'Source Sans Pro', sans-serif;
  color      : var(--purple);
  background : var(--white);
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height   : auto;
}

*,
::before,
::after {
  box-sizing: border-box;
}

@media (max-width: 767px) {
  .page-body {
    font-size: 14px;
  }
}
