.page-header {
  background-color: var(--grey);
  background      : linear-gradient(to right, var(--grey) 453px, transparent 0);
}

.page-header__wrapper {
  display        : flex;
  justify-content: space-between;
  align-items    : center;
  padding-top    : 29px;
  padding-bottom : 32px;
}

.page-header__actions {
  display    : flex;
  align-items: center;
  z-index    : 3;
}

.page-header__logo {
  margin-right: 45px;
}

.page-header__menu {
  position: absolute;
  top     : 0;
  right   : 0;
  left    : 0;
  bottom  : 0;
  z-index : 2;

  &::before {
    content         : "";
    position        : absolute;
    width           : 100%;
    top             : 95px;
    height          : 1px;
    background-color: #F7F8FC;
    z-index         : 1;
  }
}

@media (max-width: 768px) {
  .page-header {
    background: linear-gradient(to right, var(--grey) 166px, transparent 0);
  }
}

@media (max-width: 767px) {
  .page-header__wrapper {
    padding-top   : 20px;
    padding-bottom: 19px;
  }

  .page-header {
    background: linear-gradient(to right, var(--grey) 99px, transparent 0);
  }

  .page-header__logo {
    margin-right: 0;
  }

  .page-header__menu::before {
    top: 75px;
  }
}
