:root {
  --white: #FFFFFF;
  --black: #000000;
  --purple: #4E254F;
  --purple-a-25: rgba(78, 37, 79, 0.25);
  --green: #9ACCC2;
  --grey-hero: #eaeef1;
  --grey: #F7F8FC;
  --red: #ED4156;
}
.page-body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 24px;
  font-family: 'Source Sans Pro', sans-serif;
  color: var(--purple);
  background: var(--white);
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
*,
::before,
::after {
  box-sizing: border-box;
}
@media (max-width: 767px) {
  .page-body {
    font-size: 14px;
  }
}
.mobile-container {
  width: 280px;
  margin: 0 auto;
  padding: 0 20px;
}
.tablet-container {
  width: 708px;
  padding: 0 30px;
}
.desktop-container {
  width: 1220px;
  padding: 0 40px;
}
.container {
  width: 1800px;
  padding: 0 20px;
  margin: 0 auto;
}
.container--hero {
  align-self: end;
  width: 1560px;
  margin: 0 auto;
}
.container--menu {
  display: grid;
}
@media (max-width: 1800px) {
  .container,
  .container--hero {
    width: 1560px;
    padding: 0 33px;
  }
}
@media (max-width: 1560px) {
  .container,
  .container--hero {
    width: 1174px;
    padding: 0 33px;
  }
}
@media (max-width: 1279px) {
  .container,
  .container--hero {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .container,
  .container--hero {
    padding: 0 24px;
  }
}
.lang-list {
  display: flex;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}
.lang-list__link {
  display: block;
  padding: 8px 13px;
  font-size: 14px;
  line-height: 16px;
  color: var(--purple-a-25);
  transition: 0.35s;
}
.lang-list__link--active {
  color: var(--purple);
}
.lang-list__link:hover {
  color: var(--purple);
}
@media (max-width: 768px) {
  .lang-list {
    visibility: hidden;
    transition: 0.2s;
  }
  .nav-open .lang-list {
    visibility: visible;
  }
}
.page-header {
  background-color: var(--grey);
  background: linear-gradient(to right, var(--grey) 453px, transparent 0);
}
.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 29px;
  padding-bottom: 32px;
}
.page-header__actions {
  display: flex;
  align-items: center;
  z-index: 3;
}
.page-header__logo {
  margin-right: 45px;
}
.page-header__menu {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 2;
}
.page-header__menu::before {
  content: "";
  position: absolute;
  width: 100%;
  top: 95px;
  height: 1px;
  background-color: #F7F8FC;
  z-index: 1;
}
@media (max-width: 768px) {
  .page-header {
    background: linear-gradient(to right, var(--grey) 166px, transparent 0);
  }
}
@media (max-width: 767px) {
  .page-header__wrapper {
    padding-top: 20px;
    padding-bottom: 19px;
  }
  .page-header {
    background: linear-gradient(to right, var(--grey) 99px, transparent 0);
  }
  .page-header__logo {
    margin-right: 0;
  }
  .page-header__menu::before {
    top: 75px;
  }
}
.logo {
  display: block;
  width: 125px;
  height: 35px;
}
@media (max-width: 676px) {
  .logo {
    width: 97px;
    height: 23px;
  }
}
.toggle-menu {
  display: block;
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
  justify-self: end;
}
.toggle-menu--menu {
  transition: transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1), opacity 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.nav-open .toggle-menu--menu {
  transform: scale(0);
  opacity: 0;
}
.toggle-menu--cross {
  margin-top: 21px;
  margin-right: -12px;
  transform: scale(0);
  transition: transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1), opacity 0.6s cubic-bezier(0.075, 0.82, 0.165, 1), stoke 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
  opacity: 0;
  z-index: 1;
}
.menu--open .toggle-menu--cross {
  transform: scale(1);
  opacity: 1;
}
@media (max-width: 767px) {
  .toggle-menu--cross {
    margin-top: 10px;
  }
}
.hero__wrapper {
  display: flex;
  flex-direction: column;
}
.hero {
  display: grid;
  grid-template-rows: 1fr min-content;
  min-height: 100%;
  background-color: var(--grey-hero);
  background-image: url("../img/bg-hero.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center bottom;
}
.hero__promo {
  max-width: 799px;
  padding-top: 80px;
  padding-left: 40px;
  padding-right: 105px;
  padding-bottom: 128px;
  background-color: var(--green);
}
.hero__title {
  margin: 0;
  margin-bottom: 32px;
  font-weight: 900;
  font-size: 60px;
  line-height: 72px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}
.hero__text {
  margin: 0;
}
@media (max-width: 1560px) {
  .hero__promo {
    width: 54%;
  }
}
@media (max-width: 1280px) {
  .hero__promo {
    padding-top: 56px;
    padding-bottom: 56px;
  }
}
@media (max-width: 1279px) {
  .hero__promo {
    padding-left: 24px;
    padding-right: 24px;
  }
  .hero__title {
    margin-bottom: 24px;
    font-size: 42px;
    line-height: 130%;
  }
  .hero__text {
    font-size: 14px;
  }
}
@media (max-width: 768px) {
  .hero {
    background-image: url("../img/bg-hero-tablet.jpg");
  }
}
@media (max-width: 767px) {
  .hero__promo {
    width: 100%;
    order: -1;
    margin-bottom: 15px;
  }
  .hero__wrapper {
    padding-bottom: 87px;
  }
  .hero__title {
    margin-bottom: 8px;
    font-size: 30px;
  }
}
@media (max-width: 375px) {
  .hero {
    background-image: url("../img/bg-hero-mobile.jpg");
  }
}
.page {
  height: 100%;
}
.page-body {
  display: grid;
  grid-template-rows: min-content 1fr;
  min-height: 100%;
}
.button {
  position: relative;
  display: inline-grid;
  grid-auto-flow: column;
  column-gap: 52px;
  align-items: center;
  padding: 20px 32px;
  background-color: var(--purple);
  color: var(--white);
  text-transform: uppercase;
  transition: background-color 0.6s cubic-bezier(0.075, 0.82, 0.165, 1), color 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.button::after {
  content: "";
  position: absolute;
  top: 0;
  left: 176px;
  width: 1px;
  height: 100%;
  background-color: var(--white);
  transform: skewX(-17deg);
}
.button::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
  transition: transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
  background-color: var(--green);
}
.button:hover::before {
  transform: scaleX(1);
  transform-origin: center left;
}
.button--hero {
  margin-left: auto;
}
.button:hover .button__icon {
  transform: translateX(0.33333rem);
  transition-delay: 0.1s;
}
.button__icon {
  fill: currentColor;
  transition: transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.button__text {
  position: relative;
}
@media (max-width: 500px) {
  .button--hero {
    padding: 12px 20px 12px 80px;
    margin-left: 0;
    justify-content: space-between;
  }
  .button::after {
    left: 265px;
  }
}
@media (max-width: 359px) {
  .button--hero {
    padding: 12px 20px 12px 20px;
    margin-left: 0;
    justify-content: end;
  }
  .button::after {
    left: 225px;
  }
}
.splide {
  margin-left: auto;
}
.splide__slide {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding-left: 56px;
  background: var(--purple);
}
.splide__slide::after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border-right: 90px solid transparent;
  border-top: 256px solid var(--purple);
  right: 334px;
}
.splide__descr {
  max-width: 450px;
  padding-top: 104px;
}
.splide__title {
  margin: 0;
  margin-bottom: 16px;
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  color: var(--green);
}
.splide__text {
  margin: 0;
  color: var(--green);
  opacity: 0.75;
}
.splide__pagination {
  top: 56px;
  bottom: auto;
  left: 56px;
  width: auto;
  transform: translateX(0);
  counter-reset: pagination;
}
.splide__pagination__page {
  width: 18px;
  height: 24px;
  counter-increment: pagination;
  background: transparent;
  opacity: 0.25;
}
.splide__pagination__page::before {
  content: counter(pagination, decimal-leading-zero);
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: var(--white);
  opacity: 0.25;
}
.splide__pagination__page.is-active {
  background: transparent;
  opacity: 1;
  transform: scale(1);
}
.splide__pagination__page.is-active::before {
  font-weight: 700;
  color: var(--green);
  opacity: 1;
}
.splide__arrow svg {
  width: 16px;
  height: 16px;
  fill: none;
}
.splide__arrow {
  width: 44px;
  height: 64px;
  border: 1px solid var(--white);
  border-radius: 0;
  background: transparent;
  opacity: 1;
  transition: 0.35s;
}
.splide__arrow:hover {
  border-color: var(--green);
  background-color: var(--green);
}
.splide__arrow--prev,
.splide__arrow--next {
  transform: skew(-20deg) translateY(-50%);
}
.splide__arrow--prev {
  left: -86%;
}
.splide__arrow--next {
  right: auto;
  left: -79%;
}
@media (max-width: 1560px) {
  .splide__slide {
    padding-bottom: 56px;
  }
  .splide__slide img,
  .splide__slide::after {
    display: none;
  }
}
@media (max-width: 1440px) {
  .splide__arrow--prev {
    left: -88%;
  }
}
@media (max-width: 1280px) {
  .splide__slide {
    padding-bottom: 24px;
  }
  .splide__descr {
    padding-top: 60px;
  }
  .splide__pagination {
    top: 24px;
  }
  .splide__arrow--prev {
    left: -99%;
  }
  .splide__arrow--next {
    left: -88%;
  }
  .splide__pagination {
    left: 51px;
  }
}
@media (max-width: 1279px) {
  .splide__slide {
    padding-right: 32px;
    padding-left: 32px;
  }
  .splide__title {
    font-size: 18px;
    margin-bottom: 2px;
  }
  .splide__text {
    font-size: 14px;
  }
  .splide__pagination {
    left: 30px;
  }
  .splide__container {
    max-width: 100vw;
  }
}
@media (max-width: 768px) {
  .splide__arrow--prev {
    left: -107%;
  }
}
@media (max-width: 767px) {
  .splide__container {
    order: -1;
    align-self: end;
    max-width: 100vw;
    padding: 0 24px;
    margin: 0 auto;
  }
  .splide {
    margin-left: 0;
  }
  .splide__title {
    font-size: 14px;
    line-height: 24px;
    margin-bottom: 6px;
  }
  .splide__pagination__page::before {
    font-size: 14px;
  }
}
.menu {
  visibility: hidden;
  transition: visibility 0s 0.8s;
}
.menu--open {
  visibility: visible;
  transition-delay: 0s;
}
.menu__list {
  margin: 0;
  padding: 0;
  padding-top: 132px;
  list-style: none;
}
.menu__link {
  font-size: 30px;
  line-height: 40px;
  color: var(--purple-a-25);
  transition: 0.35s;
}
.menu__link--current,
.menu__link:hover {
  color: var(--purple);
}
.menu__link--powered {
  font-size: 12px;
  line-height: 16px;
  color: var(--purple);
}
.menu__link--company {
  color: var(--red);
}
.menu__item--powered {
  margin-top: 220px;
}
.menu__item:not(:last-child) {
  margin-bottom: 32px;
}
.menu__container {
  transform: translateY(-2rem);
  transition: opacity 0.6s cubic-bezier(0.075, 0.82, 0.165, 1), transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
  opacity: 0;
}
.menu--open .menu__container {
  opacity: 1;
  transition-delay: 0.2s;
  transform: translateY(0);
}
.menu--open .menu__nav::before {
  transform: scaleY(1);
  transition-delay: 0s;
}
.menu__nav::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--white);
  transform: scaleY(0);
  transform-origin: center top;
  transition: transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1) 0.2s;
}
@media (max-width: 1560px) {
  .menu__link {
    font-size: 26px;
    line-height: 36px;
  }
  .menu__item--powered {
    margin-top: 80px;
  }
  .menu__link--powered {
    font-size: 12px;
    line-height: 16px;
  }
}
@media (max-width: 767px) {
  .menu__list {
    padding-top: 35px;
  }
  .menu__link {
    display: block;
    font-size: 14px;
    line-height: 26px;
    text-align: center;
  }
  .menu__link--powered {
    font-size: 10px;
    line-height: 16px;
  }
  .menu__item--powered {
    margin-top: 70px;
  }
}
