.menu {
  visibility: hidden;
  transition: visibility 0s .8s;


  &--open {
    visibility      : visible;
    transition-delay: 0s;
  }
}

.menu__list {
  margin     : 0;
  padding    : 0;
  padding-top: 132px;
  list-style : none;
}

.menu__link {
  font-size  : 30px;
  line-height: 40px;
  color      : var(--purple-a-25);
  transition : 0.35s;

  &--current,
  &:hover {
    color: var(--purple);
  }

  &--powered {
    font-size  : 12px;
    line-height: 16px;
    color      : var(--purple)
  }

  &--company {
    color: var(--red);
  }
}

.menu__item {
  &--powered {
    margin-top: 220px;
  }

  &:not(:last-child) {
    margin-bottom: 32px;
  }
}

.menu__container {
  transform : translateY(-2rem);
  transition: opacity .6s cubic-bezier(.075, .82, .165, 1), transform .6s cubic-bezier(.075, .82, .165, 1);
  opacity   : 0;
}

.menu--open .menu__container {
  opacity         : 1;
  transition-delay: 0.2s;
  transform       : translateY(0);
}

.menu--open .menu__nav::before {
  transform       : scaleY(1);
  transition-delay: 0s;
}

.menu__nav::before {
  content         : "";
  position        : absolute;
  top             : 0;
  right           : 0;
  bottom          : 0;
  left            : 0;
  background-color: var(--white);
  transform       : scaleY(0);
  transform-origin: center top;
  transition      : transform .6s cubic-bezier(.075, .82, .165, 1) .2s;
}


@media (max-width: 1560px) {
  .menu__link {
    font-size  : 26px;
    line-height: 36px;
  }

  .menu__item--powered {
    margin-top: 80px;
  }

  .menu__link {
    &--powered {
      font-size  : 12px;
      line-height: 16px;
    }
  }
}


@media (max-width: 767px) {
  .menu__list {
    padding-top: 35px;
  }

  .menu__link {
    display    : block;
    font-size  : 14px;
    line-height: 26px;
    text-align : center;

    &--powered {
      font-size  : 10px;
      line-height: 16px;
    }
  }

  .menu__item--powered {
    margin-top: 70px;
  }
}
