.lang-list {
  display   : flex;
  height    : 100%;
  margin    : 0;
  padding   : 0;
  list-style: none;
}

.lang-list__link {
  display    : block;
  padding    : 8px 13px;
  font-size  : 14px;
  line-height: 16px;
  color      : var(--purple-a-25);
  transition : 0.35s;

  &--active {
    color: var(--purple);
  }

  &:hover {
    color: var(--purple);
  }
}

@media (max-width: 768px) {
  .lang-list {
    visibility: hidden;
    transition: 0.2s;
  }

  .nav-open .lang-list {
    visibility: visible;
  }
}
