.button {
  position        : relative;
  display         : inline-grid;
  grid-auto-flow  : column;
  column-gap      : 52px;
  align-items     : center;
  padding         : 20px 32px;
  background-color: var(--purple);
  color           : var(--white);
  text-transform  : uppercase;
  transition      : background-color .6s cubic-bezier(.075, .82, .165, 1), color .6s cubic-bezier(.075, .82, .165, 1);

  &::after {
    content         : "";
    position        : absolute;
    top             : 0;
    left            : 176px;
    width           : 1px;
    height          : 100%;
    background-color: var(--white);
    transform       : skewX(-17deg);
  }

  &::before {
    content         : "";
    position        : absolute;
    top             : 0;
    right           : 0;
    bottom          : 0;
    left            : 0;
    transform       : scaleX(0);
    transition      : transform .6s cubic-bezier(.075, .82, .165, 1);
    background-color: var(--green);
  }


  &:hover::before {
    transform       : scaleX(1);
    transform-origin: center left;
  }

  &--hero {
    margin-left: auto;
  }

  &:hover .button__icon {
    transform       : translateX(.33333rem);
    transition-delay: .1s;
  }
}

.button__icon {
  fill      : currentColor;
  transition: transform .6s cubic-bezier(.075, .82, .165, 1);
}

.button__text {
  position: relative;
}

@media (max-width: 500px) {
  .button--hero {
    padding        : 12px 20px 12px 80px;
    margin-left    : 0;
    justify-content: space-between;
  }

  .button::after {
    left: 265px;
  }
}

@media (max-width: 359px) {
  .button--hero {
    padding        : 12px 20px 12px 20px;
    margin-left    : 0;
    justify-content: end;
  }

  .button::after {
    left: 225px;
  }
}
