.container {
  width  : 1800px;
  padding: 0 20px;
  margin : 0 auto;

  &--hero {
    align-self: end;
    width     : 1560px;
    margin    : 0 auto;
  }

  &--menu {
    display: grid;
  }
}

@media (max-width: 1800px) {

  .container,
  .container--hero {
    width  : 1560px;
    padding: 0 33px;
  }
}


@media (max-width: 1560px) {

  .container,
  .container--hero {
    width  : 1174px;
    padding: 0 33px;
  }
}

@media (max-width: 1279px) {

  .container,
  .container--hero {
    width: 100%;
  }
}

@media (max-width: 767px) {

  .container,
  .container--hero {
    padding: 0 24px;
  }
}
